import * as yup from "yup";
const upperCase = /(^[-]|[A-Z])/;
export const schemaForProfileSettings = yup.object().shape({
  name: yup
    .string()
    .trim()
    .matches(
      upperCase,
      "        "
    )
    .min(2, "     2 "),

  lastName: yup
    .string()
    .trim()
    .matches(
      upperCase,
      "        "
    )
    .min(2, "     2 "),
  brieflyAboutYourself: yup
    .string()
    .trim()
    .matches(
      upperCase,
      "     (   )    "
    )
    .min(10, "  '  '    10 "),
  aboutMySelf: yup
    .string()
    .trim()
    .matches(
      upperCase,
      "     (   )    "
    )
    .min(2, " ' '     2 ")
    .max(500, " ' '    500 "),
  linkToContacts: yup
    .string()
    .trim()
    .min(2, " ''    2 ")
    .max(40, " ''    40 "),
});
