import * as yup from "yup";
const upperCase = /(^[-])/;
const questionMarkAtTheEnd = /(\?$)/;
export const schemaForAskQuestions = yup.object().shape({
  question_title: yup
    .string()
    .matches(
      upperCase,
      "     (   )    "
    )
    .min(15, "      15   150 ")
    .matches(
      questionMarkAtTheEnd,
      "  ,     "
    )
    .required("  "),
  question_tags: yup.string().max(10, "   "),

  question_details: yup
    .string()
    .matches(
      upperCase,
      "     (   )    "
    )
    .min(30, "      30   10  ")
    .required("  "),
});
