import * as yup from "yup";
export const schemaForSignUp = yup.object().shape({
  email: yup
    .string()
    .email("     ")
    .required("  "),
  nickName: yup
    .string()
    .min(4, "    4 ")
    .required(" "),
  password: yup
    .string()
    .min(4, "    4 ")
    .required(" "),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref("password"), null], "  ")
    .required(" "),
});
